@import '../../styles/main.scss';
.gridCard {
  padding: 0px;
  display: flex;
  /* flex-wrap: wrap; */
  flex-direction: column;
  margin-top: 40px;
  gap: 40px;
  width: 100%;
  margin: auto;
  justify-content: center;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-top: 62px;
    gap: 0;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 60px;
    flex-wrap: nowrap;
  }
}

.card_recept {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media screen and (min-width: 768px) {
    align-items: center;
    /*width: 342px;*/
    max-width: 400px;
    margin-left: 20px;
    margin-top: 80px;
    /* outline: 2px solid rgb(255, 225, 0); */
    &:nth-child(2n + 1) {
      margin-left: 0px;
    }
    &:nth-child(-n + 2) {
      margin-top: 0px;
    }
  }
  @media screen and (min-width: 1200px) {
    margin-left: 20px;
    margin-top: 80px;
    /* outline: 2px solid rgb(255, 225, 0); */
    &:nth-child(2n + 1) {
      margin-left: 20px;
    }
    &:nth-child(-n + 2) {
      margin-top: 80px;
    }
    &:nth-child(3n + 1) {
      margin-left: 0px;
    }
    &:nth-child(-n + 3) {
      margin-top: 0px;
    }
  }
  @media screen and (min-width: 1440px) {
    width: 400px;
  }
}

.container_img {
  display: flex;
  border-radius: 8px;
  margin-bottom: 24px;
  overflow: hidden;
  width: 100%;

  @media screen and (min-width: 768px) {
    /*width: 342px;
    height: 360px;*/
    width: 100%;
    max-width: 400px;
    max-height: 400px;
  }
  @media screen and (min-width: 1200px) {
    max-width: 400px;
    max-height: 400px;
  }
  @media screen and (min-width: 1440px) {
    width: 400px;
    height: 400px;
  }
}

.recipe {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 14px;
  max-width: 335px;
  @media screen and (min-width: 768px) {
    max-width: 342px;
  }
  @media screen and (min-width: 1200px) {
    max-width: 400px;
  }
}

.drink_name {
  margin-right: 0;
  color: #f3f3f3;
  font-family: Manrope;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.open_recipe {
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  color: rgba(243, 243, 243, 0.5);
  // margin-left: auto;
  // margin-right: auto;
}
